.container{
    width: 100%;
    height:80%;
  
    padding-top: 150px;
    margin-top: 50px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    top:40px;
    left:0;
    z-index: -20;
    
    
}

.details{
    display: flex;
    flex-direction: column;
    width: 100%;
    height:100%;
    position: relative;
    top:0px;
    padding-left:50px;
    


}



.details_more{
   
    padding:25px 0px;
    padding-left: 30px;
    width: 90%;
    height: calc(100% - 50px);
    display: flex;
    justify-content: center;
    background-color: aliceblue;
 
    
  
}
.details_more>div:nth-child(1){
    width:50%;
    display: flex;
    justify-content: start; 
    align-items:flex-start; 
   
}




.details_more>div:nth-child(2){
    width:50%;
    display: flex;
    justify-content: center; 
    align-items:flex-start; 
 
}



.pokemonname{
    width: 100%;
    font-size: 30px;
    font-weight: bold;
    color: #e65100 ;
    margin-left:  0px;
    position: relative;
    margin-top: -40px;
}

.abilities_and_specs{
    display: flex;
    flex-direction: column;
}

.abilities_div{
    border-bottom: 1px solid black;
    padding-bottom: 10px;
    margin-top:20px;


    
}

.ability{
    display: flex;
    position: relative;
    left:-20px;
}


.pos_absolute{
    position: absolute;
    top: 60px;
    left: 40px;
}
