.search_cont{
    position: absolute;
    top:105px;
    width:100vw;
    
    display:flex;
    justify-content: center;
    
}

.search_cont input{
    width: 60%;
    padding:10px;
    border: 2px solid #e65100;
    outline-width: 2px;
    outline-color: #e65100;
    border-radius: 5px;
    color: #e65100;
    font-weight: bold;
   
}

::-webkit-input-placeholder { /* Edge */
    color: #2e7d32;
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #2e7d32;
  }
  
  ::placeholder {
    color: #2e7d32;
  }


.search_cont button{
    margin-left: 25px;
    width: 90px;
    border: 2px solid #2e7d32;
    background-color: white;
    color: #2e7d32;
    border-radius: 5px;

}

.search_cont button:hover{
    cursor: pointer;
    background-color: #2e7d32;
    color: white;
    font-weight: 700;
}